
#app2 .tab-bar{
    display: flex;
}
#app2 .tab-bar > li{
    width: 50%;
    border: 1px solid black;
    text-align: center;
}
#app2 .tab-bar > li.selected{
    background: orange;
    color: white;
}
#app2 .tab-content > li{
    display: none;
}
#app2 .tab-content > li.active{
    display: block;
}
